<template>
  <div class="box">
    <div class="wrap">
      <div class="con">
        <p class="image"><img src="@/assets/logo.png" alt="" /></p>
        <p class="ton">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="username">
              <el-input
                v-model="ruleForm.username"
                prefix-icon="el-icon-user-solid"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input
                v-model="ruleForm.password"
                prefix-icon="el-icon-lock"
              ></el-input>
            </el-form-item>

            <el-form-item class="btn-box">
              <el-button type="primary" @click="submitForm('ruleForm')"
                >登录</el-button
              >
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: "长度在 3 到 15 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: "长度在 3 到 15 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {},
  mounted() {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("submit!");
          this.ajaxLogin();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //登录方法
    async ajaxLogin() {
      let { data } = await this.$http.post(
        "/api/private/v1/login",
        this.ruleForm
      );
      // console.log(data);
      if (data.meta.status == 200) {
        window.sessionStorage.setItem("token", data.data.token);
        this.$message({
          message: "登录成功",
          duration:1000,
          type: "success",
        });
         this.$router.push("/index");
      } else {
        this.$message({
          message: "登录失败",
           duration:1000,
          type: "error",
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
}
.box {
  width: 100%;
  height: 100%;
  .wrap {
    width: 100%;
    height: 580px;
    background-color: #2b4b6b;
    .con {
      width: 600px;
      height: 300px;
      background-color: #fff;
      position: fixed;
      top: 25%;
      left: 25%;
      position: relative;
      .image {
        position: absolute;
        top: -80px;
        left: 220px;
        z-index: 10;
        img {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          background-color: #f0f0f0;
        }
      }
      .ton {
        position: absolute;
        bottom: 20px;
        left: 0;
        .el-form demo-ruleForm {
          margin: 15px 20px;
        }
        .el-input--prefix {
          width: 400px;
        }
        .btn-box {
          display: flex;
          justify-content: flex-end;
        }
      }
      .btn {
        position: absolute;
        bottom: 30px;
        right: 50px;
      }
    }
  }
}
</style>
