Vue.use(VueRouter);

const routes = [
  // {
  //   path: "/",
  //   name: "home",
  //   redirect:"/deng"
  // },
  // {
  //   path: "/about",
  //   name: "About",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: function() {
  //     return import(/* webpackChunkName: "about" */ "../views/About.vue");
  //   },
  // },
 
  // {
  //   path: '/:id',
  //   name: 'Home',
  //   component: Home
  // },
  {
    path: "/",
    name: "deng",
    component: dl,
  },
  {
    path: "/index",
    name: "index",
    redirect: "/welcome",
    component: () => {
      return import("../views/box/index.vue");
    },
    beforeEnter(to, from, next) {
      if (window.sessionStorage.getItem("token")) {
        next();
      } else {
        next("/deng");
      }
    },
    children: [
      {
        path: "/welcome",
        name: "welcome",
        component: () => {
          return import("../views/box/Welcome.vue");
        },
      },
      {
        path: "/users",
        name: "users",
        component: () => {
          return import("../views/box/Users.vue");
        },
      },
      {
        path: "/roles",
        name: "roles",
        component: () => {
          return import("../views/box/Roles.vue");
        },
      },
    ],
  },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
